import React, { useState, useEffect } from 'react';
import Fade from 'react-reveal/Fade';
import { Container, Row, Col } from 'react-bootstrap';
import Title from '../components/Title';

const About = () => {
  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="about">
      <Container>
        {isDesktop && (
          <>
            <Title title="Yuki Nishidate / 西舘祐樹" />
            <Fade right duration={500} delay={250} distance="30px">
              <p>{'コンピュータグラフィックスが好きな大学生です。'}</p>
              <br></br>
              <h2>{'学歴'}</h2>
              <p>{'・ 慶應義塾湘南藤沢高等部 卒業'}</p>
              <p>{'・ 慶應義塾大学 理工学部 情報工学科 在学中'}</p>
              <br></br>
              <h2>{'職歴'}</h2>
              <p>{'・ 家具広告制作会社で3Dデザイナーとしてインターン'}</p>
              <p>{'・ デジタル・フロンティアでR&Dエンジニアとしてアルバイト'}</p>
              <br></br>
              <h2>{'業績'}</h2>
              <p>{'・ DHU U18アーティストコンテストで準優勝'}</p>
              <p>{'・ CGエンジニア検定の成績に基づいて文部科学大臣賞受賞'}</p>
              <p>{'・ 研究業績については今後に期待...'}</p>
            </Fade>
          </>
        )}
        {isMobile && (
          <Fade left={isDesktop} bottom={isMobile} duration={500} delay={250} distance="30px">
            <div>
              <Title title="Yuki Nishidate" />
              <p>
                {
                  '慶應義塾大学 理工学部 情報工学科の学部生です。コンピュータグラフィックスに興味があります。'
                }
              </p>
            </div>
          </Fade>
        )}
      </Container>
    </section>
  );
};

export default About;
